import "./styles.css";
import React from "react";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      generateCode: "Generated Password"
    };
  }

  changeColor = () => {
    this.setState({ color: "blue" });
  };

  copyToClipboard = () => {
    console.log("Logging");
    navigator.clipboard.writeText(this.state.generateCode);

    /* Alert the copied text */
    alert("Copied the text: " + this.state.generateCode);
  };

  render() {
    return (
      <div className="App">
        <h1 className="Pad">
          <b>S</b>ecure<b>M</b>e
        </h1>
        <img
          src="../kerberos.jpeg"
          alt="Problems"
          height="300"
          width="250"
        ></img>
        <center>
          <table className="Pad" cellPadding="10">
            <tr>
              <td> Secret Key </td>
              <td>
                {" "}
                <input type="text"></input>{" "}
              </td>
            </tr>
            <tr>
              <td> Website Name </td>
              <td>
                {" "}
                <input type="text"></input>{" "}
              </td>
            </tr>
            <tr className="Middle">
              <td colSpan="2">
                <text class="dotted" id="code">
                  {" "}
                  {this.state.generateCode}{" "}
                </text>
                <button class="btn" onClick={this.copyToClipboard}>
                  <i class="fa fa-copy"></i>
                </button>
              </td>
            </tr>
          </table>
          <button type="button" class="btn btn-success">
            Generate
          </button>
        </center>
      </div>
    );
  }
}
